:root {
  --primary-color: #424549;
  --text-color: aliceblue;
}

.App {
  text-align: center;
}

.Main {
 height: 100vh !important;
 width: 100vw !important;
}

body {
  background-color: var(--primary-color);
  color: var(--text-color);
}

button {
  background-color: black;
  color: var(--text-color);
  border-radius: 5px;
}


.modal {
  z-index: 1;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  background-color:var(--primary-color);
}

.modal-body {
  overflow-y: auto;
  max-height: 90vh;
  padding: 5px;
}
